
.vue-container {
  margin: 0px auto;
  max-width: 700px;
  overflow: hidden;
}
.vue-container .divider {
    background: #fff;
    height: 1px;
}
.vue-container .divider.divider-short {
      max-width: 40px;
}
.vue-container .vue-player {
    background: #000;
    height: 258px;
    overflow: hidden;
    position: relative;
    width: 100%;
}
.vue-container .vue-player > div, .vue-container .vue-player > video {
      height: 100%;
      position: absolute;
      width: 100%;
      left: 0;
}
.vue-container .vue-player .cover {
      background-repeat: no-repeat;
      background-image: url("http://img.kaiyanapp.com/5bbdff1ebe422035f85b117c4aabf51c.jpeg?imageMogr2/quality/60/format/jpg");
      background-position: bottom;
      background-size: cover;
}
.vue-container .vue-player .play-button {
      background-color: rgba(0, 0, 0, 0.15);
      background-image: url(//static.kaiyanapp.com/eyepetizer-web/assets/images/detail/icon_play@2x.3b9f9ba5.png);
      background-position: 50%;
      background-repeat: no-repeat;
      background-size: 60px 60px;
      cursor: pointer;
      transition: background-color .5s;
}
.vue-container .vue-player .play-button:hover {
        background-color: rgba(0, 0, 0, 0.5);
}
@media (min-width: 700px) {
.vue-container .vue-player {
        height: calc(700px / 16 * 9);
}
}
.vue-container .vue-video-info {
    position: relative;
}
.vue-container .vue-video-info .vue-meta-positioner .video-meta {
      padding: 15px;
      padding-bottom: 4px;
      background: rgba(0, 0, 0, 0.2);
}
.vue-container .vue-video-info .vue-meta-positioner .video-meta h1 {
        font-size: 20px;
        margin: 0;
        margin-bottom: 7px;
}
.vue-container .vue-video-info .vue-meta-positioner .video-meta .divider {
        margin-bottom: 7px;
}
.vue-container .vue-video-info .vue-meta-positioner .video-meta p {
        margin: 0;
        margin-bottom: 12px;
}
.vue-container .vue-video-info .vue-meta-positioner .video-meta p.desciption {
          margin-bottom: 11px;
          opacity: .85;
}
.vue-container .vue-video-info .relate-video-list {
      background: rgba(0, 0, 0, 0.4);
      padding: 20px 15px 10px;
}
.vue-container .vue-video-info .relate-video-list .relate-video {
        margin-bottom: 20px;
        position: relative;
}
.vue-container .vue-video-info .relate-video-list .relate-video .relate-cover {
          background-position: 50%;
          background-size: cover;
          border: 1px solid rgba(255, 255, 255, 0.3);
          padding-top: 25%;
          width: 40%;
}
.vue-container .vue-video-info .relate-video-list .relate-video .meta {
          left: 40%;
          margin-left: 15px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          color: #FFF;
}
.vue-container .vue-video-info .relate-video-list .relate-video .meta .title {
            font-size: 13px;
            font-weight: 700;
            margin-bottom: 5px;
}
.vue-container .vue-video-info .relate-video-list .relate-video .meta .title + .category {
              font-size: 12px;
              opacity: .85;
}
@media (min-width: 400px) {
.vue-container .vue-video-info .relate-video-list .relate-video .meta .title {
                font-size: 14px;
}
.vue-container .vue-video-info .relate-video-list .relate-video .meta .title + .category {
                  font-size: 13px;
}
}
.vue-container .vue-video-info .reply-list-container {
      background: rgba(0, 0, 0, 0.4);
}
.vue-container .vue-video-info .reply-list-container .text-center {
        padding: 16px 0 10px;
}
.vue-container .vue-video-info .reply-list-container .reply {
        font-size: 12px;
        padding-bottom: 15px;
        position: relative;
}
.vue-container .vue-video-info .reply-list-container .reply .avatar {
          background-position: 50%;
          background-size: cover;
          border-radius: 50%;
          float: left;
          height: 36px;
          margin: 0 13px;
          width: 36px;
}
.vue-container .vue-video-info .reply-list-container .reply .avatar + .entity {
            margin-left: 62px;
}
.vue-container .vue-video-info .reply-list-container .reply .avatar + .entity p {
              margin: 0px;
}
.vue-container .vue-video-info .reply-list-container .reply .avatar + .entity p.username {
                margin-bottom: 4px;
}
.vue-container .vue-video-info .reply-list-container .reply .avatar + .entity p.time {
                margin-bottom: 4px;
                opacity: .6;
}
.vue-container .vue-video-info .reply-list-container .reply .avatar + .entity p.text {
                margin-bottom: 15px;
                padding-right: 35px;
}
.vue-container .vue-video-info .reply-list-container .reply .avatar + .entity + .like {
              position: absolute;
              text-align: right;
              top: 0;
              right: 16px;
}
.vue-container .vue-video-info .reply-list-container .reply .avatar + .entity + .like .like-count > img {
                width: 15px;
                height: 15px;
                margin-left: 3px;
}
.vue-container .vue-video-info .reply-list-container .reply .avatar + .entity + .like .like-count + .like-count-hot {
                font-family: Lobster,cursive;
}
.vue-container .vue-video-info .tag-list-container {
      background: rgba(0, 0, 0, 0.4);
}
.vue-container .vue-video-info .tag-list-container .text-center {
        padding: 16px 0px;
}
.vue-container .vue-video-info .tag-list-container .tag-list {
        padding: 0 37px 16px;
        text-align: center;
}
.vue-container .vue-video-info .tag-list-container .tag-list .tag {
          background: rgba(255, 255, 255, 0.3);
          border-radius: 2px;
          display: inline-block;
          font-size: 12px;
          letter-spacing: 3px;
          line-height: 22px;
          margin: 4px;
          padding: 0 6px 0 9px;
}
.vue-container .vue-video-info .video-cover-blurred {
      background-position: 50%;
      background-size: 100% 100%;
      height: 100%;
      position: absolute;
      top: 0;
      transform: rotate(180deg) scaleX(-1);
      width: 100%;
      z-index: -10;
      background-image: url(http://img.kaiyanapp.com/3a3c7d91d928303382fced649951387e.jpeg?imageMogr2/quality/60/format/jpg);
}
.vue-container .vue-video-info .vue-footer {
      background: rgba(0, 0, 0, 0.4);
      padding: 40px 0 60px;
}
.vue-container .vue-video-info .vue-footer .qrcode {
        display: block;
        margin: 0 auto 20px;
        width: 80px;
}
.vue-container .vue-video-info .vue-footer .qr-tips {
        background: transparent;
        color: #fff;
        font-size: 12px;
        letter-spacing: 1px;
        text-align: center;
}
.vue-container .vue-video-info .vue-footer div {
        background-size: cover;
        display: block;
        margin: 0 auto 40px;
}
.vue-container .vue-video-info .vue-footer div.bottom-logo {
          background-image: url(//static.kaiyanapp.com/eyepetizer-web/assets/images/detail/bottom_logo@3x.556cae6b.png);
          height: 94px;
          width: 90px;
}



